<template>
  <div class="app-container">
    <div class="app-side app-side-left">
      <sidebar :data="sidebardata" :open="defultopen"></sidebar>
    </div>
    <div class="app-right" style="overflow-y: auto; position:relative">
      <router-view name="appRight"></router-view>
    </div>
  </div>
</template>

<script>
import sidebar from "@/components/sidebar";
export default {
  data() {
    return {
      defultopen: [
        "/networkOpAnalysis",
        "/networkOpAnalysis/networkDevice"
      ],
      sidebardata: {
        name: "网络优化分析",
        path: "/networkOpAnalysis",
        children: [
          {
            name: '用户套餐',
            path: "/networkOpAnalysis/eleven",
            children: [
              {
                name: "OLT可承载用户数",
                path: "/networkOpAnalysis/ten/ts10_3"
              },
              {
                name: "特定用户数下可分配带宽及套餐带宽建议",
                path: "/networkOpAnalysis/ten/ts10_4"
              },
            ]
          },
          {
            name: "规划方案",
            path: "/networkOpAnalysis/eleven",
            children: [
              {
                name: "设备能力瓶颈预测",
                path: "/networkOpAnalysis/eleven/ts11_1"
              },
              {
                name: "规划方案输出",
                path: "/networkOpAnalysis/eleven/ts11_2"
              },
              {
                name: "割接至同局所其它设备建议",
                path: "/networkOpAnalysis/eleven/ts11_3"
              },
            ]
          },
          {
            name: "规划方案评估",
            path: "/networkOpAnalysis/twelve",
            children: [
              {
                name: "规划方案评估",
                path: "/networkOpAnalysis/twelve/ts12"
              },
            ]
          }
        ]
      }
    };
  },
  components: {
    sidebar
  }
};
</script>

<style>
.app-container {
  position: relative;
  padding: 0;
  margin: 0;
  height: calc(100% - 95px);
  width: 100%;
  display: flex;
}
.app-side {
  height: 100%;
  width: 250px;
  font-weight: 700;
  color: #333;
  background-color: #fff;
  /* border-right: solid 1px grey; */
  overflow-y: scroll;
}
.app-right {
  width: calc(100% - 230px);
  height: calc(100% - 40px);
  padding: 20px;
  overflow-y: scroll;
  overflow: auto;
}
</style>



