<template>
  <div>
    <h1>OLT PON 口南向设备间连接关系</h1>
    <el-select v-model="value" placeholder="请选择">
      <el-option v-for="item in options" :key="item" :label="item" :value="item"></el-option>
    </el-select>
    <el-select v-model="value" placeholder="请选择">
      <el-option v-for="item in options1" :key="item" :label="item" :value="item"></el-option>
    </el-select>
    <el-button type="primary" @click="search">查询</el-button>
    <el-table :data="tableData" style="width: 100%" height="700" :header-cell-style="header">
      <el-table-column
        v-for="(columnData,index) in tableInitData"
        :key="index"
        :prop="columnData[0]"
        :label="columnData[1]"
        width="180"
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import h from "@/components/basic/util/tableColor.js";
import axios from "axios";
export default {
  data() {
    return {
      tableInitData: [
        ["olt_id", "OLT IP"],
        ["olt_port", "OLT 端口"],
        ["OS_id", "分光器ID"],
        ["onu_ip", "onu IP"],
        ["onu_id", "onu ID"],
        ["onu_type", "onu类型"],
        ["onu_manufacture", "onu厂商"],
        ["onu_serial_number", "onu序列号"]
      ],
      tableData: [
        {
          onu_manufacture: "HW",
          onu_type: "MDU",
          onu_ip: "10.41.24.10",
          OS_id: "163943309",
          olt_id: "10.45.36.17",
          onu_serial_number: "BJ00000000",
          onu_id: "16",
          olt_port: "0/0/17/5"
        }
      ],
      options: [
        "10.45.36.16",
        "10.45.36.17",
        "10.45.36.18",
        "10.45.36.19",
        "全部"
      ],
      value: "",
      options1: [
      ],
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/Device_interface_matching_south/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    header() {
      return h;
    },
    search() {
      let value = this.value;
      if(value === '全部') {
        value = ''
      }
      axios({
        method: "get",
        url: "/api/Device_interface_matching_south/",
        params: {
          OLT_IP: value
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
        }
      });
    }
  }
};
</script>

<style>
</style>



