<template>
  <div>
    <el-input v-model="ratio"></el-input>
    <el-button @click="search" type="pramiry">查询</el-button>
    <el-table :data="tableData" style="width: 100%" height="550" :row-class-name="tableRowClassName">
      <el-table-column
        v-for="(columnData,index) in tableInitData"
        :key="index"
        :prop="columnData[0]"
        :label="columnData[1]"
        width=180
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      ratio: '',
      tableInitData: [
        ['OLT_name','OLT_name'],
        ['bandwidth','bandwidth'],
        ['device_model','device_model'],
        ['manufacturer','manufacturer'],
        ['searial_num','searial_num'],
        ['speed','speed'],
        ['total_traffic','total_traffic'],
      ],
      tableData: []
    }
  },
  methods: {
    search() {
      let ratio1 = this.ratio
      // console.log(ratio)
      axios({
        method: "get",
        url: "/api/ofc_select_user/",
        params: {
          ratio: ratio1
        }
      }).then((res) => {
        if(res.data.success) {
          console.log(res.data.data)
          this.tableData = res.data.data
        }
      })
    },
    tableRowClassName({row, rowIndex}) {
      // console.log(row,rowIndex)
        // if (rowIndex === 1) {
        //   return 'warning-row';
        // } else if (rowIndex === 3) {
        //   return 'success-row';
        // }
        // return '';
        if(row.speed === '100M' || row.speed === '200M') {
          // console.log(1)
          return 'warning-row';
        }
      }
  }
}
</script>

<style>
  .el-table .warning-row {
    background: oldlace;
  }
</style>



