<template>
  <div>
    <h1>流量链路树的上行流量压力叠加</h1>
    <el-select v-model="olt_ip" placeholder="请选择">
      <el-option
        v-for="item in olt_options"
        :key="item.OLT_IP"
        :label="item.OLT_IP"
        :value="item.OLT_IP"
      ></el-option>
    </el-select>
    <el-select v-model="olt_port" placeholder="请选择">
      <el-option
        v-for="item in olt_port_options"
        :key="item.OLT_PORT"
        :label="item.OLT_PORT"
        :value="item.OLT_PORT"
      ></el-option>
    </el-select>
    <el-select v-model="olt_mdu_ip" placeholder="请选择">
      <el-option
        v-for="(item,index) in olt_mdu_ip_options"
        :key="index"
        :label="item.MDU_ip"
        :value="item.MDU_ip"
      ></el-option>
    </el-select>
    <el-button @click="search" type="primary">查询</el-button>
    <base-table :tableInitData="tableInitData" :tableData="tableData" :width="180"></base-table>
  </div>
</template>

<script>
import axios from "axios";
import BaseTable from "@/components/basic/baseTable1";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      olt_ip: "",
      olt_options: [],
      olt_port: "",
      olt_port_options: [],
      olt_mdu_ip: "",
      olt_mdu_ip_options: [],
      tableInitData: [
        "OLT_IP",
        "OLT_PORT",
        "MDU_ip",
        "up_multiple_user_live",
        "up_multiple_user_demand",
        "up_unicast_live",
        "up_unicast_demand",
        "up_bandwidth",
        "up_sum",
        "down_multiple_user_live",
        "down_multiple_demand",
        "down_unicast_live",
        "down_unicast_demand",
        "down_bandwidth",
        "down_sum"
      ],
      tableData: []
    };
  },
  mounted() {
    axios.get("/api/show_olt_9_16/").then(res => {
      if (res.data.success) {
        this.olt_options = res.data.data;
      }
    });
  },
  watch: {
    olt_ip: function () {
      let olt_ip = this.olt_ip;
      if (olt_ip) {
        axios({
          method: "get",
          url: "/api/select_olt_and_show_pon_9_16/",
          params: {
            olt_ip: olt_ip
          }
        }).then(res => {
          if (res.data.success) {
            this.olt_port_options = res.data.data;
          }
        });
      } else {
        this.olt_port_options = [];
        this.olt_mdu_ip_options = [];
      }
    },
    olt_port: function () {
      console.log(1)
      let olt_ip = this.olt_ip;
      let olt_port = this.olt_port;
      if (olt_port) {
        axios({
          method: "get",
          url: "/api/select_pon_and_show_mdu_9_16/",
          params: {
            olt_ip: olt_ip,
            olt_port: olt_port
          }
        }).then(res => {
          console.log(res.data)
          if (res.data.success) {
            this.olt_mdu_ip_options = res.data.data;
          }
        });
      } else {
        this.olt_mdu_ip_options = [];
      }
    }
  },
  methods: {
    search() {
      let olt_ip = this.olt_ip;
      let olt_port = this.olt_port;
      let olt_mdu_ip = this.olt_mdu_ip;
      console.log(olt_ip,olt_port,olt_mdu_ip)
      axios({
        method: "get",
        url: "/api/show_datas_9_16/",
        params: {
          olt_ip: olt_ip,
          olt_port: olt_port,
          olt_mdu_ip: olt_mdu_ip
        }
      })
      .then(res => {
        if (res.data.success) {
          // this.tableData = res.data.data;
          // console.log(res.data.data)
          let re = res.data.data
          // let a = []
          // a.push(re)
          this.tableData = re
        }
      });
    }
  }
};
</script>

<style>
</style>



