<template>
    <el-table :data="tableData" style="width: 100%" height="700" :header-cell-style = "header">
      <el-table-column
        v-for="(columnData,index) in tableInitData"
        :key="index"
        :prop="columnData[0]"
        :label="columnData[1]"
        :width="width"
      ></el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="100">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
        <!-- <el-button type="text" size="small">编辑</el-button> -->
      </template>
    </el-table-column>
    </el-table>
</template>

<script>
import axios from 'axios'
// tableInitData [[prop,label]]
export default {
  props: {
    tableInitData: {
      type: Array,
      default: [["id", "id"]]
    },
    tableData: {
      type: Array,
      default: [{"id": "1"}]
    },
    width: {
      type: Number,
      default: 180
    }
  },
  methods: {
    header(row, column, rowIndex, columnIndex) {
      return {
        background:'#85C1E9',
        color: '#17202A'
      }
    },
    handleClick(r) {
      this.$emit('getRowData',r)
    }
  }
}
</script>

<style>

</style>



