<template>
  <div>
    <select-three :search="search"></select-three>
    <!-- <el-button @click="btnClickChange" type="primary">{{btnDesc}}</el-button> -->
    <el-select v-model="value">
      <el-option v-for="item in options" :key="item" :label="item" :value="item"></el-option>
    </el-select>
    <h2>{{nowDesc}}</h2>
    <el-table
      :data="portTypeData"
      style="width: 100%"
      height="700"
      v-show="port_visible"
      :header-cell-style="header"
    >
      <el-table-column prop="olt_id" label="OLTIP" width="180"></el-table-column>
      <el-table-column prop="slot_number1" label="slot_number1" width="180"></el-table-column>
      <el-table-column
        prop="trunk_or_north_port_number"
        label="trunk_or_north_port_number"
        width="180"
      ></el-table-column>
      <el-table-column prop="port_type1" label="port_type1" width="180"></el-table-column>
      <el-table-column prop="equip_type1" label="equip_type1" width="180"></el-table-column>
      <el-table-column prop="used" label="used" width="180"></el-table-column>
      <el-table-column prop="if_protected" label="是否保护专用" width="180"></el-table-column>
      <el-table-column prop="if_divided_by_service" label="是否分业务" width="180"></el-table-column>
      <el-table-column prop="if_balanced_load" label="是否负荷均衡" width="180"></el-table-column>
      <el-table-column prop="pon_or_south_port_number" label="pon_or_south_port_number" width="180"></el-table-column>
      <el-table-column prop="port_type2" label="port_type2" width="180"></el-table-column>
      <el-table-column prop="splitter" label="splitter" width="180"></el-table-column>
      <el-table-column prop="equip_id2" label="equip_id2" width="180"></el-table-column>
      <el-table-column prop="slot_number2" label="slot_number2" width="180"></el-table-column>
      <el-table-column prop="south_port_number" label="south_port_number" width="180"></el-table-column>
      <el-table-column prop="port_type_linked" label="port_type_linked" width="180"></el-table-column>
      <el-table-column prop="used2" label="used2" width="180"></el-table-column>
      <el-table-column prop="equip_type2" label="equip_type2" width="180"></el-table-column>
    </el-table>
    <el-table
      :data="equipTypeData"
      style="width: 100%"
      height="700"
      v-show="equip_visible"
      :header-cell-style="header"
    >
      <el-table-column prop="olt_id" label="OLTIP" width="180"></el-table-column>
      <el-table-column prop="slot_number1" label="slot_number1" width="180"></el-table-column>
      <el-table-column
        prop="trunk_or_north_port_number"
        label="trunk_or_north_port_number"
        width="180"
      ></el-table-column>
      <el-table-column prop="port_type1" label="port_type1" width="180"></el-table-column>
      <el-table-column prop="equip_type1" label="equip_type1" width="180"></el-table-column>
      <el-table-column prop="used" label="used" width="180"></el-table-column>
      <el-table-column prop="if_protected" label="是否保护专用" width="180"></el-table-column>
      <el-table-column prop="if_divided_by_service" label="是否分业务" width="180"></el-table-column>
      <el-table-column prop="if_balanced_load" label="是否负荷均衡" width="180"></el-table-column>
      <el-table-column prop="pon_or_south_port_number" label="pon_or_south_port_number" width="180"></el-table-column>
      <el-table-column prop="port_type2" label="port_type2" width="180"></el-table-column>
      <el-table-column prop="splitter" label="splitter" width="180"></el-table-column>
      <el-table-column prop="equip_id2" label="equip_id2" width="180"></el-table-column>
      <el-table-column prop="slot_number2" label="slot_number2" width="180"></el-table-column>
      <el-table-column prop="south_port_number" label="south_port_number" width="180"></el-table-column>
      <el-table-column prop="port_type_linked" label="port_type_linked" width="180"></el-table-column>
      <el-table-column prop="used2" label="used2" width="180"></el-table-column>
      <el-table-column prop="equip_type2" label="equip_type2" width="180"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import h from "@/components/basic/util/tableColor.js";
import selectThree from "@/components/basic/selectThree.vue";
import axios from "axios";
export default {
  data() {
    return {
      portTypeData: [
        {
          splitter: null,
          used2: "Y",
          used: "Y",
          port_type_linked: "10GE",
          slot_number1: "0/0/2",
          if_divided_by_service: "n",
          olt_id: "10.45.36.17",
          port_type2: null,
          slot_number2: "0/0/1",
          if_protected: "N",
          port_type1: "GE",
          equip_type2: "LSW",
          pon_or_south_port_number: null,
          equip_type1: "olt",
          trunk_or_north_port_number: 1,
          south_port_number: 5,
          equip_id2: "10.23.192.14",
          id: 6,
          if_balanced_load: "y"
        }
      ],
      equipTypeData: [],
      port_visible: true,
      equip_visible: false,
      btnDesc: "切换到设备类型不匹配",
      nowDesc: "端口类型不匹配表",
      value: "端口类型不匹配表",
      options: ["端口类型不匹配表", "设备类型不匹配"]
    };
  },
  methods: {
    header() {
      return h;
    },
    btnClickChange() {
      this.port_visible = !this.port_visible;
      this.equip_visible = !this.equip_visible;
      if (this.port_visible) {
        this.btnDesc = "切换到设备类型不匹配";
        this.nowDesc = "端口类型不匹配表";
      } else {
        this.btnDesc = "切换到端口类型不匹配";
        this.nowDesc = "设备类型不匹配表";
      }
    },
    search() {
      axios({
        method: "get",
        url: "/api/Device_interface_matching/"
      })
        .then(res => {
          if (res.data.success) {
            let data = res.data.data;
            this.portTypeData = data.port_type_not_match;
            this.equipTypeData = data.equip_type_not_match;
          } else {
            console.log("请求失败");
          }
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  watch: {
    value: function(e) {
      this.nowDesc = e;
      switch (e) {
        case "端口类型不匹配表":
          this.equip_visible = false;
          this.port_visible = true;
          break;
        case "设备类型不匹配":
          this.port_visible = false;
          this.equip_visible = true;
          break;
        default:
          break;
      }
    }
  },
  components: {
    selectThree
  }
};
</script>

<style>
</style>vue>



