<template>
  <div>
    <h1>PON口流量远低于阈值</h1>
    <select-three :search="search"></select-three>
    <div v-loading="loading">
      <base-table
        :tableInitData="tableInitData"
        :tableData="tableData"
        :width="180"
      ></base-table>
    </div>
  </div>
</template>

<script>
import BaseTable from '@/components/basic/baseTable'
import SelectThree from '@/components/basic/selectThree'
import axios from 'axios'
export default {
  components: {
    BaseTable,
    SelectThree
  },
  data() {
    return {
      loading: false,
      tableInitData: [
          ['olt_ip', "OLT IP"],
          ['olt_port', "PON编号"],
          ['threshold', "上联阈值"],
          ['peak_rate', '峰值端口占用率'],
          ['start_time', "开始时间点"],
          ['end_time', "种植时间点"],
          ['particle_size', "流量统计时间粒度"],
          ['user_num', '下挂用户数'],
      ],
      tableData: [
        {
          city_name: "天津",
          department_name: "东丽分公司",
          end_time: "2019/4/7 00:00",
          olt: "DLLZZ_ZX_OLT04",
          olt_ip: "10.45.36.16",
          olt_port: "0/0/5/3",
          particle_size: "1 day",
          peak_rate: 0.0242,
          start_time: "2019/4/6 00:00",
          station: "DLLZZ",
          threshold: "50%",
          user_num: 3,
        }
      ],
    };
  },
  mounted() {
  },
  methods: {
    search() {
      this.loading = true;
      let dep = this.$store.getters.getDepartment;
      let station = this.$store.getters.getStation;
      let olt = this.$store.getters.getOlt
      axios({
        method: "get",
        url: "/api/pon_port_lowwer_than_threshold/",
        params: {
          department_name: dep,
          city_name: '天津',
          station: station,
          olt_name: olt,
        }
      }).then((res) => {
        if(res.data.success) {
          this.loading = false
          this.tableData = res.data.data
        }
      })
    }
  },
}
</script>

<style>

</style>



