<template>
  <div>
    <h1>设备能力瓶颈预测</h1>
    <div class="tableWrapper11">
      <div>
        <h2>局站设备信息表</h2>
        <base-table :tableInitData="tableInitData" :tableData="tableData" :width="130"></base-table>
      </div>
      <div>
        <h2>单设备信息表</h2>
        <base-table :tableInitData="tableInitData1" :tableData="tableData1" :width="130"></base-table>
      </div>
    </div>
  </div>
</template>

<script>
import BaseTable from "@/components/basic/baseTable";
import axios from "axios";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        ["id",'id'],
        ["station",'局站'],
        ["new_switch_num",'新交换机数量'],
        ["new_OLT_up_num",'新上联口数量'],
        ["new_OLT_num",'新OLT数量'],
      ],
      tableData: [],
      tableInitData1: [
        ["id",'id'],
        ["OLT_IP",'OLT IP'],
        ["ComboPON_num",'ComboPON数量'],
        ["10GEPON_num",'10GEPON数量'],
        ["XGPON_num",'XGPON数量'],
        ["EPON_num",'EPON数量'],
        ["PON_board_num",'PON板数量'],
      ],
      tableData1: []
    };
  },
  mounted() {
    axios.get("/api/plan_11_2/").then(res => {
      if (res.data.success) {
        this.tableData = res.data.data["station"];
        this.tableData1 = res.data.data["OLT"];
      }
    });
  }
};
</script>

<style>
.tableWrapper11 {
  display: flex;
  justify-content: space-between
}
</style>



