<template>
  <div>
    <h1>套餐流量特征</h1>
    <base-table
      :tableInitData="ratio_real_table_init"
      :tableData="ratio_real_table_data"
      :width="180"
    ></base-table>
    <!-- ratio_modified_table -->
    <el-table  style="width: 100%" height="550" :data="ratio_modified_table_data" :header-cell-style = "header">
      <el-table-column prop="user_type" label="用户类型" width="180" fixed></el-table-column>
      <el-table-column prop="user_speed" label="用户速率" width="180"></el-table-column>
      <el-table-column prop="ratio" label="所占比例"></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="handleClick1(scope.row)" type="text" size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="修改参数" :visible.sync="dialogFormVisible1">
      <div>
        <el-input v-model="cu_1_ratio" placeholder="请输入所占比例" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible1 = false">取 消</el-button>
        <el-button type="primary" @click="updateNew1">确 定</el-button>
      </div>
    </el-dialog>

    <!-- modified_meal_table -->
    <el-button @click="handleClick2" type="text" size="small">修改</el-button>
    <el-table :data="modified_meal_table_data" style="width: 100%" height="550">
      <el-table-column prop="speed" label="速率" width="180" fixed></el-table-column>
      <el-table-column prop="ratio" label="所占比率" width="180"></el-table-column>
    </el-table>
    <el-dialog title="修改参数" :visible.sync="dialogFormVisible2">
      <div>
        请输入200M所占比例
        <el-input v-model="cu_200M" placeholder="请输入200M所占比例" class="elInput"></el-input>请输入300M所占比例
        <el-input v-model="cu_300M" placeholder="请输入200M所占比例" class="elInput"></el-input>请输入500M所占比例
        <el-input v-model="cu_500M" placeholder="请输入200M所占比例" class="elInput"></el-input>请输入1G所占比例
        <el-input v-model="cu_1G" placeholder="请输入200M所占比例" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false">取 消</el-button>
        <el-button type="primary" @click="updateNew2">确 定</el-button>
      </div>
    </el-dialog>

    <!-- params_table -->
    <el-button @click="handleClick3" type="text" size="small">修改</el-button>
    <base-table :tableInitData="params_table_init" :tableData="params_table_data" :width="100"></base-table>
    <el-dialog title="修改参数" :visible.sync="dialogFormVisible3">
      <div>
        请输入视频用户渗透率
        <el-input v-model="cu_vedio_user_infiltrate_rate" placeholder="请输入" class="elInput"></el-input>请输入视频并发率
        <el-input v-model="cu_vedio_concurrence_rate" placeholder="请输入" class="elInput"></el-input>请输入宽带并发率
        <el-input v-model="cu_boardband_concurrence_rate" placeholder="请输入" class="elInput"></el-input>请输入宽带业务规划用平均带宽
        <!-- <el-input v-model="cu_boardband_service_avg_bandwidth" placeholder="请输入" class="elInput"></el-input> -->
        请输入直播4K占比
        <el-input v-model="cu_4K_rate" placeholder="请输入" class="elInput"></el-input>请输入直播高清占比
        <el-input v-model="cu_HD_rate" placeholder="请输入" class="elInput"></el-input>请输入直播标清占比
        <el-input v-model="cu_SD_rate" placeholder="请输入" class="elInput"></el-input>请输入点播用户平局带宽
        <el-input v-model="cu_demand_user_avg_bandwidth" placeholder="请输入" class="elInput"></el-input>请输入高峰时直播用户占比
        <el-input v-model="cu_peak_time_live_user_rate" placeholder="请输入" class="elInput"></el-input>请输入高峰时点播用户占比
        <el-input v-model="cu_peak_time_demand_user_rate" placeholder="请输入" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible3 = false">取 消</el-button>
        <el-button type="primary" @click="updateNew3">确 定</el-button>
      </div>
    </el-dialog>
    <el-button type="primary" @click="updateAll">更新总表</el-button>
  </div>
</template>

<script>
import BaseTable from "@/components/basic/baseTable";
import h from '@/components/basic/util/tableColor.js'
import axios from "axios";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      ratio_real_table_init: [
        ["user_type", "用户类型"],
        ["user_speed", "用户速率"],
        ["peak_rate", "峰值速率（Mbps）"],
        ["ratio", "所占比例"]
      ],
      ratio_real_table_data: [
        {
          user_speed: "1G",
          ratio: "50%",
          id: 4,
          peak_rate: 10,
          user_type: "nomal_user"
        }
      ],
      ratio_modified_table_data: [
        {
          user_speed: "1G",
          ratio: "50%",
          id: 4,
          user_type: "A_user"
        }
      ],
      dialogFormVisible1: false,
      cu_1_ratio: "",
      cu_1_id: -1,
      modified_meal_table_data: [],
      dialogFormVisible2: false,
      cu_200M: "40%",
      cu_300M: "30%",
      cu_500M: "30%",
      cu_1G: "0%",
      params_table_data: [],
      dialogFormVisible3: false,
      params_table_init: [
        ["vedio_user_infiltrate_rate", "视频用户渗透率(42%)"],
        ["vedio_concurrence_rate", "视频并发率(35%)"],
        ["boardband_concurrence_rate", "宽带并发率(67%)"],
        ["boardband_service_avg_bandwidth", "宽带业务规划用平均带宽(0.83)"],
        ["4K_rate", "直播4K占比(0%)"],
        ["HD_rate", "直播高清占比(23%)"],
        ["SD_rate", "直播标清占比(77%)"],
        ["demand_user_avg_bandwidth", "点播用户平局带宽(77%)"],
        ["peak_time_live_user_rate", "高峰时直播用户占比(83.77%)"],
        ["peak_time_demand_user_rate", "高峰时点播用户占比(16.23%)"]
      ],
      cu_vedio_concurrence_rate: "42%",
      cu_4K_rate: "0%",
      cu_boardband_service_avg_bandwidth: 0.83,
      cu_demand_user_avg_bandwidth: 4.81,
      cu_boardband_concurrence_rate: "67%",
      cu_peak_time_demand_user_rate: "16.23%",
      cu_peak_time_live_user_rate: "83.77%",
      cu_HD_rate: "23%",
      cu_SD_rate: "77%",
      cu_vedio_user_infiltrate_rate: "35%",
      cu_type: "实测参考值"
    };
  },
  mounted() {
    axios.get("/api/pre_show_table_9_1/").then(res => {
      let re = res.data.data;
      this.ratio_real_table_data = re.ratio_real_table;
      this.ratio_modified_table_data = re.ratio_modified_table;
      this.modified_meal_table_data = re.meal_ratio;
      this.params_table_data = re.params_table.slice(1);
      this.mealSync(re.meal_ratio);
      this.paramsSync(re.params_table[1]);
    });
  },
  methods: {
    mealSync(data) {
      this.cu_200M = data[0].ratio;
      this.cu_300M = data[1].ratio;
      this.cu_500M = data[2].ratio;
      this.cu_1G = data[3].ratio;
    },
    paramsSync(data) {
      this.cu_vedio_concurrence_rate = data.vedio_concurrence_rate;
      this.cu_4K_rate = data["4K_rate"];
      this.cu_boardband_service_avg_bandwidth = data.boardband_service_avg_bandwidth;
      this.cu_demand_user_avg_bandwidth = data.demand_user_avg_bandwidth;
      this.cu_boardband_concurrence_rate = data.boardband_concurrence_rate;
      this.cu_peak_time_demand_user_rate = data.peak_time_demand_user_rate;
      this.cu_peak_time_live_user_rate = data.peak_time_live_user_rate;
      this.cu_HD_rate = data.HD_rate;
      this.cu_SD_rate = data.SD_rate;
      this.cu_vedio_user_infiltrate_rate = data.vedio_user_infiltrate_rate;
      this.cu_type = data.type;
    },
    handleClick1(e) {
      this.dialogFormVisible1 = true;
      this.cu_1_id = e.id;
      this.cu_1_ratio = e.ratio;
    },
    handleClick2() {
      this.dialogFormVisible2 = true;
    },
    handleClick3() {
      this.dialogFormVisible3 = true;
    },
    updateNew1() {
      axios({
        method: "get",
        url: "/api/pre_modified_ratio_table_9_1/",
        params: {
          id: this.cu_1_id,
          ratio: this.cu_1_ratio
        }
      }).then(res => {
        if (res.data.success) {
          let re = res.data.data;
          this.ratio_modified_table_data = re.ratio_modified_table;
          this.modified_meal_table_data = re.meal_ratio;
          this.params_table_data = re.params_table.slice(1);
          this.mealSync(re.meal_ratio);
          alert("修改成功");
          this.dialogFormVisible1 = false;
        } else {
          alert("修改失败");
          this.dialogFormVisible1 = false;
        }
      });
    },
    updateNew2() {
      axios({
        method: "get",
        url: "/api/pre_modified_meal_table_9_1/",
        params: {
          "200M": this.cu_200M,
          "300M": this.cu_300M,
          "500M": this.cu_500M,
          "1G": this.cu_1G
        }
      }).then(res => {
        if (res.data.success) {
          let re = res.data.data;
          this.ratio_modified_table_data = re.ratio_modified_table;
          this.modified_meal_table_data = re.meal_ratio;
          this.params_table_data = re.params_table.slice(1);
          alert("修改成功");
          this.dialogFormVisible2 = false;
        } else {
          alert("修改失败");
          this.dialogFormVisible2 = false;
        }
      });
    },
    updateNew3() {
      axios({
        method: "get",
        url: "/api/pre_modified_params_table_9_1/",
        params: {
          vedio_concurrence_rate: this.cu_vedio_concurrence_rate,
          '4K_rate': this.cu_4K_rate,
          // boardband_service_avg_bandwidth: this.cu_boardband_service_avg_bandwidth,
          demand_user_avg_bandwidth: this.cu_demand_user_avg_bandwidth,
          boardband_concurrence_rate: this.cu_boardband_concurrence_rate,
          peak_time_demand_user_rate: this.cu_peak_time_demand_user_rate,
          peak_time_live_user_rate: this.cu_peak_time_live_user_rate,
          HD_rate: this.cu_HD_rate,
          SD_rate: this.cu_SD_rate,
          vedio_user_infiltrate_rate: this.cu_vedio_user_infiltrate_rate,
          type: this.cu_type,
        }
      }).then(res => {
        if (res.data.success) {
          let re = res.data.data;
          this.ratio_modified_table_data = re.ratio_modified_table;
          this.modified_meal_table_data = re.meal_ratio;
          this.params_table_data = re.params_table.slice(1);
          this.mealSync(re.meal_ratio);
          this.paramsSync(re.params_table[1]);
          alert("修改成功");
          this.dialogFormVisible3 = false;
        } else {
          alert("修改失败");
          this.dialogFormVisible3 = false;
        }
      });
    },
    updateAll() {
      axios.get('/api/update_user_table_9_1/').then((res) => {
        if(res.data.success) {
          alert('修改成功')
        }
      })
    },
    header() {
      return h
    },
  }
};
</script>

<style>
</style>



