<template>
  <div>
    <base-table :tableInitData="tableInitData" :tableData="tableData" :width="180"></base-table>
  </div>
</template>

<script>
import axios from "axios";
import BaseTable from "@/components/basic/baseTable";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        ["olt_id", "OLT IP"],
        ["service_slot_count", "槽位数"],
        ["PON_num",'单PON板使用端口总数'],
        ["max_user_num",'最大用户数'],
        ["now_user_num",'下挂用户数'],
        ["remain_user_",'剩余可开通用户数'],
      ],
      tableData: []
    };
  },
  mounted() {
    axios.get("/api/OLT_allow_usernum_10_3/").then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  }
};
</script>

<style>
</style>



