<template>
  <div>
    <h1>BRAS流量-时间趋势</h1>
    <span>纵坐标单位：Mbps</span>
    <base-line-chart
      :width="800"
      :height="600"
      :title="title"
      :legend="legend"
      :xData="xData"
      :yData="yData"
      :id = "chart1"
    >
    </base-line-chart>
  </div>
</template>

<script>
import BaseLineChart from "@/components/basic/baseChart/baseLineChart";
import axios from 'axios'
export default {
  components: {
    BaseLineChart
  },
  data() {
    return {
      title: "",
      legend: [],
      xData: [],
      yData: [
        {
          name: "",
          type: "line",
          data: []
        }
      ],
      chart1: 'chart1'
    };
  },
  mounted() {
    axios({
      method: 'get',
      url: '/api/bras_8_3/'
    })
    .then((res) => {
      if(res.data.success) {
        // console.log(res.data.data)
        let re = res.data.data
        // let x = []
        // let y_out = []
        // let y_in = []
        // re.map((item,index) => {
        //   x.push(`day${index + 1}`)
        //   y_out.push(item.traffic)
        // })
        this.legend = ['out','in']
        // this.xData = x
        // this.yData = [{
        //   name: 'CDN',
        //   type: "line",
        //   data: y
        // }]
        console.log(re[0].day_out)
        let x = ['2019/06/01','2019/06/02','2019/06/03','2019/06/04','2019/06/05','2019/06/06','2019/06/07']
        this.xData = x
        this.yData = [{
          name: 'out',
          type: "line",
          data: re[0].day_out
        },{
          name: 'in',
          type: "line",
          data: re[0].day_in
        }]
      }
    })
  },
  methods: {
  },
};
</script>

<style>
</style>



