import userActionAnalysis from '../userActionAnalysis'


import Ts31 from '@/components/task/three/ts31'
import Ts32 from '@/components/task/three/ts32'

import oltUsersCount from "@/components/networkResources/OLT/oltUsersCount"
import usersStatA from "@/components/networkResources/OLT/usersStatA"
import usersStatIPTV from "@/components/networkResources/OLT/usersStatIPTV"
import usersStat from "@/components/networkResources/OLT/usersStat"


const routes = [
  {
    path: '/userActionAnalysis',
    name: '网络资源分析',
    components: {
      businessModel: userActionAnalysis
    },
    children: [
      {
        path: 'userInfo/ts31',
        name: '任务3-1',
        components: {
          appRight: Ts31
        }
      },
      {
        path: 'userInfo/ts32',
        name: '任务3-2',
        components: {
          appRight: Ts32
        }
      },
      // OLT用户分布
      {
        path: 'userDistribution/oltUsersCount',
        name: 'OLT下挂用户分布',
        components: {
          appRight: oltUsersCount
        },
      },
      {
        path: 'userDistribution/usersStatA',
        name: '分业务带宽的用户数分布',
        components: {
          appRight: usersStatA
        },
      },
      {
        path: 'userDistribution/usersStatIPTV',
        name: 'IPTV用户数分布',
        components: {
          appRight: usersStatIPTV
        },
      },
      {
        path: 'userDistribution/usersStat',
        name: '分业务类型的用户数分布',
        components: {
          appRight: usersStat
        },
      },
    ]
  }
]
export default routes



