<template>
  <div>
    <h1>网络能力链路树</h1>
    <div style="margin: 10px;height: 50px">
      请输入宽带账号：
      <el-input v-model="Broadband_account" style="width: 300px"></el-input>请输入OLT IP：
      <el-input v-model="olt_id" style="width: 300px"></el-input>
      <el-button type="primary" @click="search">查询</el-button>
    </div>
    <el-table :data="tableData" style="width: 100%" height="550" :header-cell-style="header">
      <el-table-column
        v-for="(columnData,index) in tableInitData"
        :key="index"
        :prop="columnData[0]"
        :label="columnData[1]"
        width="110"
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import h from "@/components/basic/util/tableColor.js";
import axios from "axios";
export default {
  data() {
    return {
      tableInitData: [
        ["Broadband_account", "宽带帐号"],
        ["bandwidth", "带宽"],
        ["IPTV_account", "IPTV号"],
        ["MDUIP", "MDU IP"],
        ["MDU_port", "MDU号"],
        ["OS_id", "分光器ID"],
        ["olt_id", "OLT IP"],
        ["olt_down_port", "OLT PON口号"],
        ["choosed_olt_up_port", "OLT上联口组"],
        ["LSW_IP", "交换机IP"],
        ["LSW_down_port", "交换机南向口"],
        ["choosed_lsw_up_port", "交换机北向口"],
        ["BRAS_IP", "BRAS_IP"],
        ["BRAS_port", "BRAS_port"]
        // ["other_equip_IP", "其他设备IP"],
        // ["equip_port", "设备号"],
      ],
      tableData: [
        {
          onu_manufacture: "HW",
          onu_type: "MDU",
          onu_ip: "10.41.24.10",
          OS_id: "163943309",
          olt_id: "10.45.36.17",
          onu_serial_number: "BJ00000000",
          onu_id: "16",
          olt_port: "0/0/17/5"
        }
      ],
      Broadband_account: "",
      olt_id: ""
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/link_tree/",
      params: {
        Broadband_account: "",
        olt_id: ""
      }
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    header() {
      return h;
    },
    search() {
      axios({
        method: "get",
        url: "/api/link_tree/",
        params: {
          Broadband_account: this.Broadband_account,
          olt_id: this.olt_id
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
        }
      });
    }
  }
};
</script>

<style>
</style>



