<template>
  <div id="app">
    <div class="header">
      <div class="mainTitle">宽带接入网效能平台</div>
      <div class="login_out">
        <span>{{this.userName}}</span>
        <a href="javascript:void(0)" @click="loginOut">login_out</a>
      </div>
    </div>
    <el-menu
      :default-active="'/'"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#4a6793"
      text-color="#fff"
      active-text-color=""
      :router="router"
    >
      <el-menu-item index="/">首页</el-menu-item>
      <el-menu-item index="/networkResourceAnalysis">网络资源分析</el-menu-item>
      <el-menu-item index="/flowTrendAnalysis">流量趋势分析</el-menu-item>
      <el-menu-item index="/userActionAnalysis">用户行为分析</el-menu-item>
      <el-menu-item index="/networkBottleneckAnalysis">网络瓶颈分析</el-menu-item>
      <el-menu-item index="/networkOpAnalysis">网络优化分析</el-menu-item>
      <el-menu-item index="/system">系统管理</el-menu-item>
    </el-menu>

    <!-- <el-breadcrumb v-if='$route.path!=="/"' style="height:35px;line-height:35px;padding-left:20px;font-weight: bold;font-size:16px" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item style="color：white" :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item style="color：white">{{$route.matched[0].name}}</el-breadcrumb-item>
      <el-breadcrumb-item style="color：white" v-if="$route.matched[1]">{{$route.matched[1].name}}</el-breadcrumb-item>
    </el-breadcrumb>-->
    <router-view name="homePage"></router-view>
    <router-view name="businessModel"></router-view>
    <router-view name="login"></router-view>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      router: true,
      showmenu: true,
      selectMenu1: "",
      selectMenu2: "",
      userName: "未登录",
      menus: [
        {
          firstMenu: "业务模型",
          secondMenu: [
            { name: "业务模型", id: "businessModel" },
            { name: "用户行为习惯", id: "userHabits" }
          ]
        },
        {
          firstMenu: "全网资源展示",
          secondMenu: [
            { name: "网络架构", id: "networkArchitecture" },
            { name: "地图展示", id: "map" },
            { name: "网络资源统计", id: "networkResources" },
            { name: "总表下载", id: "chartDownload" }
          ]
        },
        {
          firstMenu: "业务流量采集及分析",
          secondMenu: [
            { name: "业务流量采集", id: "trafficCollect" },
            { name: "业务流量分析", id: "trafficAnalysis" },
            { name: "业务流量估算", id: "trafficEstimate" }
          ]
        },
        {
          firstMenu: "现网承载能力分析",
          secondMenu: [
            { name: "现网承载能力分析", id: "capacityAnalysis" },
            { name: "业务对网络承载能力分析", id: "serviceCapacityAnalysis" }
          ]
        },
        {
          firstMenu: "规划建设方案评估",
          secondMenu: [{ name: "规划建设方案评估", id: "planEvaluation" }]
        },
        {
          firstMenu: "新任务",
          secondMenu: [{ name: "任务", id: "Task" }]
        }
      ]
    };
  },
  watch: {
    $route(to, from) {
      console.log(this.$route);
      this.$store.dispatch("getNewMenu", this.$route);
      if (this.$route.path == "/") {
        this.showmenu = true;
      } else {
        this.showmenu = false;
      }
    }
  },
  computed: {},
  updated() {
    // 要加判断逻辑
    // this.$router.push('/login')
    // console.log()
    this.userName = this.$store.getters.getUserName;
  },
  methods: {
    loginOut() {
      this.$store.dispatch("getNewIsLogin", false);
      this.$router.push("/login");
    }
  }
};
</script>

<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: relative;

}
.header {
  position: relative;
  width: 100%;
  height: 60px;
  text-align: center;
  padding: 0;
  background: url(./assets/title.png) no-repeat;
  background-color: #4a6793;
  background-size: 100% 100%;
  color: black;
}
.login_out {
  position: absolute;
  top: 5px;
  right: 5px;
}
.mainTitle {
  float: center;
  margin-left: 0px;
  font-size: 2em;
  font-weight: bold;
  color: white;
}
#mainMenu {
  width: 100%;
  height: 35px;
  background-color: #4a6793;
  overflow: hidden;
}
#menus {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 35px;
  width: 100%;
  background-color: #4a6793;
}
#menus li {
  float: left;
  width: 15%;
  height: 35px;
  overflow: hidden;
}

#menus li a {
  display: block;
  color: white;
  font-size: bold;
  text-align: center;
  line-height: 35px;
  text-decoration: none;
  overflow: hidden;
  width: 100%;
  height: 35px;
}
#menus li ul {
  line-height: 35px;
  list-style: none;
  text-align: center;
  width: 15%;
  height: 35px;
  position: absolute;
  z-index: 2;
  display: none;
  margin: 0;
  padding: 0;
}
#menus li ul li {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 35px;
  border-bottom: solid 1px #f1f1f1;
  overflow: hidden;
  text-align: center;
  background-color: rgb(145, 172, 214);
  color: #333;
  font-weight: bold;
}
#menus li ul li:last-child {
  border-bottom: none;
}
#menus li ul li:hover {
  background: #6b98e6;
}
/* .mainContent {
  width: 96%;
  margin-top:40px;
  margin-left:2%;
  margin-right:2%;
  height: 700px;
  display: flex;
  position: relative;
}
.mainleft{
  width: 18%;
  height: 680px;
  position: relative;
}
.mainc{
    width:80%;
    height:680px;
    padding-left:1%;
    position: relative;
    flex:1
} */
.el-submenu__title {
  padding-left: 0;
}
.el-breadcrumb__inner {
  color: white;
}
.el-breadcrumb__inner.is-link {
  color: white;
}
.el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: white;
}
.el-menu-item {
  width: 215px;
  font-size: 20px;
  text-align: center
}
.el-menu {
  /* padding: 0 10px */
}

</style>



