<template>
  <div>
    <el-upload
      class="upload-demo"
      ref="upload"
      action
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :file-list="fileList"
      :auto-upload="false"
    >
      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
      <div slot="tip" class="el-upload__tip">只能上传excel文件，且不超过500kb</div>
    </el-upload>
    <base-table :tableInitData="tableInitData" :tableData="tableData" :width="180"></base-table>
  </div>
</template>

<script>
import axios from "axios";
import BaseTable from "@/components/basic/baseTable";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        ["id",'id'],
        ["OLT_IP",'OLT IP'],
        ["ComboPON_num",'ComboPON数量'],
        ["10GEPON_num",'10GEPON数量'],
        ["XGPON_num",'XGPON数量'],
        ["EPON_num",'EPON数量'],
        ["PON_board_num",'PON板数量'],
      ],
      tableData: [],
      fileList: []
    };
  },
  mounted() {},
  methods: {
    search() {
      axios.get("/api/plan_12_1/").then(res => {
        if (res.data.success) {
          this.tableData = res.data.data["OLT"];
        }
      });
    },
    submitUpload() {
      this.$refs.upload.submit();
      this.search()
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  }
};
</script>

<style>
</style>



