<template>
  <div>
    <h1>CDN下行流量-时间趋势</h1>
    <span>纵坐标单位：Gbps</span>
    <base-line-chart
      :width="800"
      :height="600"
      :title="title"
      :legend="legend"
      :xData="xData"
      :yData="yData"
      :id = "chart1"
    >
    </base-line-chart>
  </div>
</template>

<script>
import BaseLineChart from "@/components/basic/baseChart/baseLineChart";
import axios from 'axios'
export default {
  components: {
    BaseLineChart
  },
  data() {
    return {
      title: "",
      legend: [],
      xData: [],
      yData: [
        {
          name: "",
          type: "line",
          data: []
        }
      ],
      chart1: 'chart1'
    };
  },
  mounted() {
    axios({
      method: 'get',
      url: '/api/CDN_8_3/'
    })
    .then((res) => {
      if(res.data.success) {
        console.log(res.data.data)
        let re = res.data.data
        let x = []
        let y = []
        re.map((item) => {
          x.push(item.date)
          y.push(item.traffic)
        })
        this.legend = ['CDN']
        this.xData = x
        this.yData = [{
          name: 'CDN',
          type: "line",
          data: y
        }]
      }
    })
  },
  methods: {
  },
};
</script>

<style>
</style>



