import flowTrendAnalysis from '../flowTrendAnalysis'
import vendorCount from "@/components/networkResources/OLT/vendorCount"
import modelCount from "@/components/networkResources/OLT/modelCount"

import FlowDistribution from '@/components/networkResources/USER_NEED/FlowDistribution'
import PonFlowSpeed from '@/components/networkResources/USER_NEED/PonFlowSpeed'
import DiffCombPonSpeed from '@/components/networkResources/USER_NEED/DiffCombPonSpeed'
import PonFlowTrend from '@/components/networkResources/USER_NEED/PonFlowTrend'
import Mealcomparison from '@/components/networkResources/USER_NEED/Mealcomparison'
import HighUserMeal from '@/components/networkResources/USER_NEED/HighUserMeal'
import TrendDiff from '@/components/networkResources/USER_NEED/TrendDiff'

import Ts8_3_1 from '@/components/task/eight/ts8_3_1'
import Ts8_3_2 from '@/components/task/eight/ts8_3_2'
import Ts8_3_3 from '@/components/task/eight/ts8_3_3'
import Ts8_5_1 from '@/components/task/eight/ts8_5_1'

import K_means from '../k_means/k_means'
import Find_high_user from '../k_means/find_high_user'

import Ts8_2_1 from '@/components/task/eight/ts8_2_1'
import Ts8_2_2 from '@/components/task/eight/ts8_2_2'
import Ts8_2_3 from '@/components/task/eight/ts8_2_3'
import Ts8_2_4 from '@/components/task/eight/ts8_2_4'

const routes = [
  {
    path: '/flowTrendAnalysis',
    name: '网络资源分析',
    components: {
      businessModel: flowTrendAnalysis
    },
    children: [
      // 流量分析
      {
        path: 'flow/flowDistrobution',
        components: {
          appRight: FlowDistribution
        }
      },
      {
        path: 'flow/PonFlowSpeed',
        components: {
          appRight: PonFlowSpeed
        }
      },
      {
        path: 'flow/DiffCombPonSpeed',
        components: {
          appRight: DiffCombPonSpeed
        }
      },
      {
        path: 'flow/PonFlowTrend',
        components: {
          appRight: PonFlowTrend
        }
      },
      {
        path: 'flow/Mealcomparison',
        components: {
          appRight: Mealcomparison
        }
      },
      {
        path: 'flow/HighUserMeal',
        components: {
          appRight: HighUserMeal
        }
      },
      {
        path: 'flow/TrendDiff',
        components: {
          appRight: TrendDiff
        }
      },
      // 时间趋势
      {
        path: 'timeTrend/ts8_3_1',
        name: '任务8-3-1',
        components: {
          appRight: Ts8_3_1
        }
      },
      {
        path: 'timeTrend/ts8_3_2',
        name: '任务8-3-2',
        components: {
          appRight: Ts8_3_2
        }
      },
      {
        path: 'timeTrend/ts8_3_3',
        name: '任务8-3-3',
        components: {
          appRight: Ts8_3_3
        }
      },
      {
        path: 'timeTrend/ts8_5_1',
        name: '任务8-5-1',
        components: {
          appRight: Ts8_5_1
        }
      },
      // K-means
      {
        path: 'Kmeans/k1',
        name: '聚类',
        components: {
          appRight: K_means
        }
      },
      {
        path: 'Kmeans/find_high_user',
        name: '高流量用户挖掘',
        components: {
          appRight: Find_high_user
        }
      },
      // 超限
      {
        path: 'abnormal/ts8_2_1',
        name: '任务8-2-1',
        components: {
          appRight: Ts8_2_1
        }
      },
      {
        path: 'abnormal/ts8_2_2',
        name: '任务8-2-2',
        components: {
          appRight: Ts8_2_2
        }
      },
      {
        path: 'abnormal/ts8_2_3',
        name: '任务8-2-3',
        components: {
          appRight: Ts8_2_3
        }
      },
      {
        path: 'abnormal/ts8_2_4',
        name: '任务8-2-4',
        components: {
          appRight: Ts8_2_4
        }
      },
    ]
  }
]
export default routes



