<template>
  <div>
    <h1>FTTH下行用户来源压力</h1>
    <base-line-chart
      :width="800"
      :height="600"
      :title="'下行峰值'"
      :legend="legend"
      :xData="xData"
      :yData="yData_1"
      :id="chart1"
    ></base-line-chart>
    <base-line-chart
      :width="800"
      :height="600"
      :title="'PON口数'"
      :legend="legend"
      :xData="xData"
      :yData="yData_2"
      :id="chart2"
    ></base-line-chart>
    <base-line-chart
      :width="800"
      :height="600"
      :title="'用户数'"
      :legend="legend"
      :xData="xData"
      :yData="yData_3"
      :id="chart3"
    ></base-line-chart>
    <base-line-chart
      :width="800"
      :height="600"
      :title="'下行均值'"
      :legend="legend"
      :xData="xData"
      :yData="yData_4"
      :id="chart4"
    ></base-line-chart>
  </div>
</template>

<script>
import BaseLineChart from "@/components/basic/baseChart/baseLineChart";
import axios from "axios";
export default {
  components: {
    BaseLineChart
  },
  data() {
    return {
      title: "",
      legend: [],
      xData: [],
      yData_1: [
      ],
      yData_2: [

      ],
      yData_3: [

      ],
      yData_4: [
      ],
      chart1: "chart1",
      chart2: "chart2",
      chart3: "chart3",
      chart4: "chart4"
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/PON_traffic_trend_line_8_4/"
    }).then(res => {
      if (res.data.success) {
        // console.log(res.data.data);
        let re = res.data.data;
        let x = [];
        let y1 = [];
        let y2 = [];
        let y3 = [];
        let y4 = [];
        re.map(item => {
          x.push(item.DATE);
          y1.push(item.Downstream_peak);
          y2.push(item.Pon_port_count);
          y3.push(item.User_count);
          y4.push(item.downstream_avg);
        });
        // console.log(x)
        // this.legend = ["CDN"];
        this.xData = x;
        this.yData_1 = [
          {
            name: "峰值速率",
            type: "line",
            data: y1
          }
        ];
        this.yData_2 = [
          {
            name: "PON口数量",
            type: "line",
            data: y2
          }
        ];
        this.yData_3 = [
          {
            name: "用户数量",
            type: "line",
            data: y3
          }
        ];
        this.yData_4 = [
          {
            name: "均值速率",
            type: "line",
            data: y4
          }
        ];
      }
    });
  }
};
</script>

<style>
</style>



