<template>
<div>
  <h1>割接至同局所其它设备建议</h1>
  <base-table :tableInitData="tableInitData" :tableData="tableData" :width="180"></base-table>
</div>
</template>

<script>
import BaseTable from "@/components/basic/baseTable";
import axios from 'axios'
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        ['id','id'],
        ['station','局站'],
        ['device_A_IP','割接设备IP'],
        ['over_threshold_port','超过阈值端口'],
        ['device_B_IP','迁移设备IP'],
        ['spare_port_num','空余端口数']
      ],
      tableData: []
    }
  },
  mounted() {
    axios.get("/api/cut_11_3/").then(res => {
      if (res.data.success) {
        this.tableData = res.data.data
      }
    });
  }
}
</script>

<style>

</style>



