<template>
  <div>
    <div>
      <div class="areaCheckWrapper">
        <!-- <span></span> -->
        <el-checkbox-group v-model="checkboxGroup1">
          <el-checkbox-button v-for="dep in deps" :label="dep" :key="dep">{{dep}}</el-checkbox-button>
        </el-checkbox-group>
      </div>
      <div class="areaCheckWrapper">
        <el-checkbox-group v-model="checkboxGroup2">
          <el-checkbox-button v-for="dep in deps" :label="dep" :key="dep">{{dep}}</el-checkbox-button>
        </el-checkbox-group>
      </div>
      <el-select v-model="group_count" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.value"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
    <el-button @click="search">查询</el-button>
    <div class="areaWrapper">
      <div id="areaChart_1" class="area_chart">1</div>
      <div id="areaChart_2" class="area_chart">2</div>
    </div>
  </div>
</template>

<script>
import echarts from "echarts";
import Axios from "axios";
export default {
  data() {
    return {
      checkboxGroup1: [],
      checkboxGroup2: [],
      deps: [
        "东丽分公司",
        "北辰分公司",
        "南开分公司",
        "和平分公司",
        "塘沽分公司",
        "大港分公司",
        "宁河分公司",
        "宝坻分公司",
        "武清分公司",
        "汉沽分公司",
        "河东分公司",
        "河北分公司",
        "河西分公司",
        "津南分公司",
        "红桥分公司",
        "蓟县分公司",
        "西青分公司",
        "静海分公司"
      ],
      depMap: {
        东丽分公司: 1,
        北辰分公司: 2,
        南开分公司: 3,
        和平分公司: 4,
        塘沽分公司: 5,
        大港分公司: 6,
        宁河分公司: 7,
        宝坻分公司: 8,
        武清分公司: 9,
        汉沽分公司: 10,
        河东分公司: 11,
        河北分公司: 12,
        河西分公司: 13,
        津南分公司: 14,
        红桥分公司: 15,
        蓟县分公司: 16,
        西青分公司: 17,
        静海分公司: 18
      },
      group1: "",
      group2: "",
      options: [
        {
          value: "1"
        },
        {
          value: "2"
        },
        {
          value: "3"
        },
        {
          value: "4"
        },
        {
          value: "5"
        },
        {
          value: "6"
        },
      ],
      group_count: "",
      area1Data: [],
      area2Data: []
    };
  },
  watch: {
    checkboxGroup1: function(e) {
      this.group1 = "";
      e.forEach(ele => {
        this.group1 = this.group1 + this.depMap[ele] + ",";
      });
    },
    checkboxGroup2: function(e) {
      this.group2 = "";
      e.forEach(ele => {
        this.group2 = this.group2 + this.depMap[ele] + ",";
      });
    }
  },
  mounted() {
    // this.initChart('areaChart_1')
    // this.initChart('areaChart_2')
  },
  methods: {
    search() {
      Axios({
        method: "get",
        url: "/api/cluster/",
        params: {
          area1: this.group1,
          area2: this.group2,
          group_count: this.group_count
        }
      }).then((res) => {
        console.log(res.data.data)
        let re = res.data.data
        let series1 = []
        re.area1.forEach((ele,index) => {
          let a = {}
          a.name = `group${index + 1}`
          a.type = 'scatter'
          a.data = ele
          a.symbolSize = 5
          a.large = true
          series1.push(a)

        })
        let legend1 = []
        let len = re.area1.length
        for(let i = 0;i <= len;i++) {
          legend1.push(`group${i + 1}`)
        }

        let series2 = []
        re.area2.forEach((ele,index) => {
          let a = {}
          a.name = `group${index + 1}`
          a.type = 'scatter'
          a.data = ele
          a.symbolSize = 5
          a.large = true
          series2.push(a)
        })
        let legend2 = []
        let len2 = re.area2.length
        for(let i = 0;i <= len2;i++) {
          legend2.push(`group${i + 1}`)
        }
        this.initChart('areaChart_1',legend1,series1)
        this.initChart('areaChart_2',legend2,series2)
      })
    },
    initChart(id,legend,series) {
      let myChart = echarts.init(document.getElementById(id));
      let option = {
        title : {
            text: 'area you selected',
        },
        grid: {
            left: '3%',
            right: '7%',
            bottom: '3%',
            containLabel: true
        },
        tooltip : {
            // trigger: 'axis',
            showDelay : 0,
            // formatter : function (params) {
            //     if (params.value.length > 1) {
            //         return params.seriesName + ' :<br/>'
            //         + params.value[0] + 'cm '
            //         + params.value[1] + 'kg ';
            //     }
            //     else {
            //         return params.seriesName + ' :<br/>'
            //         + params.name + ' : '
            //         + params.value + 'kg ';
            //     }
            // },
            axisPointer:{
                show: true,
                type : 'cross',
                lineStyle: {
                    type : 'dashed',
                    width : 1
                }
            }
        },
        toolbox: {
            feature: {
                dataZoom: {},
                brush: {
                    type: ['rect', 'polygon', 'clear']
                }
            }
        },
        brush: {
        },
        legend: {
            data: legend,
            left: 'right',
            orient: 'vertical',
        },
        xAxis : [
            {
                type : 'value',
                scale:true,
                axisLabel : {
                    formatter: '{value}'
                },
                splitLine: {
                    show: false
                }
            }
        ],
        yAxis : [
            {
                type : 'value',
                scale:true,
                axisLabel : {
                    formatter: '{value}'
                },
                splitLine: {
                    show: false
                }
            }
        ],
        series : series
      };
      myChart.setOption(option)
      window.addEventListener("resize", function() {
        myChart.resize();
      });
    }
  }
};
</script>

<style>
.area_chart {
  width: 700px;
  height: 600px;
  border: 2px solid #99D1F7
}
.areaWrapper {
  display: flex;
  justify-content: space-around
}
.areaCheckWrapper {
  margin: 2px;
  border: 3px solid #99D1F7
}
</style>



