<template>
  <div :id="id"></div>
</template>

<script>
import echarts from "echarts";
export default {
  props: {
    width: {
      type: Number,
      default: 300
    },
    height: {
      type: Number,
      default: 300
    },
    data: Array,
    legend: Array,
    title: String,
    dataTitle: String,
    id: String,
    color: {
      type: Array,
      default: []
    },
  },
  data() {
    return {};
  },
  mounted() {
    let a = document.getElementById(this.id);
    a.style.width = this.width + "px";
    a.style.height = this.height + "px";
    this.initChart(this.title, this.legend, this.data, this.dataTitle,this.id,this.color);
  },
  methods: {
    initChart(title, legend, data, dataTitle,id,color) {
      let myChart = echarts.init(document.getElementById(id));
      let option = {

        title: {
          text: title,
          textStyle: {
            color: '#eee',
          },
          // subtext: "纯属虚构",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: legend,
          textStyle: {
            color: '#eee',
          }
        },
        series: [
          {
            name: dataTitle,
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: data,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      if(color.length>0) {
        option.color = color
      }
      myChart.setOption(option);
    },
    showChart() {
      this.initChart(this.title, this.legend, this.data, this.dataTitle,this.id,this.color);
    }
  },
  watch: {
    data: "showChart",
  }
};
/*
参数示例
https://www.echartsjs.com/examples/zh/editor.html?c=pie-simple

*/
</script>

<style>
</style>



