<template>
  <div :id="id"></div>
</template>

<script>
import echarts from "echarts";
export default {
  props: {
    width: {
      type: Number,
      default: 300
    },
    height: {
      type: Number,
      default: 300
    },
    xData: Array,
    legend: Array,
    title: String,
    yData: Array,
    id: String
  },
  data() {
    return {
    };
  },
  mounted() {
    let a = document.getElementById(this.id);
    a.style.width = this.width + "px";
    a.style.height = this.height + "px";
    this.showChart();
  },
  methods: {
    initChart(title, legend, xData, yData,id) {
      let myChart = echarts.init(document.getElementById(id));
      let option = {
        title: {
          text: title
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: legend,
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: xData
        },
        yAxis: {
          type: "value"
        },
        series: yData
      };
      myChart.setOption(option);
    },
    showChart() {
      this.initChart(this.title, this.legend, this.xData, this.yData, this.id);
    }
  },
  watch: {
    title: "showChart",
    xData: "showChart",
    legend: "showChart",
    yData: "showChart"
  }
  // computed: {
  //   show: 'showChart'
  // },
};
/*
参数示例
https://www.echartsjs.com/examples/zh/editor.html?c=line-stack

title: '折线图堆叠'
legend: ['邮件营销','联盟广告','视频广告','直接访问','搜索引擎']
xData: ['周一','周二','周三','周四','周五','周六','周日']
yData: [
        {
            name:'邮件营销',
            type:'line',
            data:[120, 132, 101, 134, 90, 230, 210]
        },
        {
            name:'联盟广告',
            type:'line',
            data:[220, 182, 191, 234, 290, 330, 310]
        },
        {
            name:'视频广告',
            type:'line',
            data:[150, 232, 201, 154, 190, 330, 410]
        },
        {
            name:'直接访问',
            type:'line',
            data:[320, 332, 301, 334, 390, 330, 320]
        },
        {
            name:'搜索引擎',
            type:'line',
            data:[820, 932, 901, 934, 1290, 1330, 1320]
        }
    ]
*/
</script>

<style>
</style>



