<template>
  <div>
    <div>9-5</div>
    <base-table :tableInitData="tableInitData" :tableData="tableData" :width="180"></base-table>
  </div>
</template>

<script>
import axios from "axios";
import BaseTable from "@/components/basic/baseTable1";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        'link_relation',
        'up_multiple_user_live',
        'up_multiple_user_demand',
        'up_unicast_live',
        'up_unicast_demand',
        'up_bandwidth',
        'up_sum',
        'down_multiple_user_live',
        'down_multiple_demand',
        'down_unicast_live',
        'down_unicast_demand',
        'down_bandwidth',
        'down_sum'
      ],
      tableData: []
    };
  },
  mounted() {
    axios.get("/api/OLT_UP_table_9_1/").then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  }
};
</script>

<style>
</style>



