<template>
  <div>
    <h1>特定用户数下可分配带宽及套餐带宽建议</h1>
    <div style="display: flex;justify-content: space-around">
      <div>
        <el-select v-model="olt_ip" placeholder="请选择">
          <el-option v-for="item in options" :key="item" :label="item" :value="item"></el-option>
        </el-select>
        <el-button type="primary" @click="search1">查询</el-button>
        <base-table1 :tableInitData="tableInitData1" :tableData="tableData1" :width="180"></base-table1>
      </div>
      <div>
        <el-input v-model="user_number" placeholder="请输入user_number" type="number" size="medium" style="width: 300px"></el-input>
        <el-input v-model="total_bandwidth" placeholder="请输入total_bandwidth" type="number" style="width: 300px"></el-input>
        <el-button type="primary" @click="search">查询</el-button>
        <base-table :tableInitData="tableInitData" :tableData="tableData" :width="280"></base-table>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import BaseTable from "@/components/basic/baseTable1";
import BaseTable1 from "@/components/basic/baseTable";
export default {
  components: {
    BaseTable,
    BaseTable1
  },
  data() {
    return {
      user_number: "",
      total_bandwidth: "",
      tableInitData: ["200M", "300M", "500M"],
      tableData: [],
      options: ["10.45.36.16", "10.45.36.17", "10.45.36.18"],
      olt_ip: "",
      tableInitData1: [['SPEED','套餐'],['count','数量']],
      tableData1: []
    };
  },
  mounted() {},
  methods: {
    search() {
      axios({
        method: "get",
        url: "/api/recommned_meal_case1_10_4/",
        params: {
          user_number: this.user_number,
          total_bandwidth: this.total_bandwidth
        }
      }).then(res => {
        if (res.data.success) {
          // console.log(res.data.data)
          let a = res.data.data;
          this.tableData1 = a;
        }
      });
    },
    search1() {
      axios({
        method: "get",
        url: "/api/select_user_by_speed_10_4/",
        params: {
          'OLT_IP': this.olt_ip
        }
      }).then(res => {
        if (res.data.success) {
          // console.log(res.data.data)
          let a = res.data.data;
          this.tableData1 = a;
        }
      });
    }
  }
};
</script>

<style>
</style>



