<template>
  <div>
    <h1>上联口流量远低于阈值</h1>
    <!-- <select-three :search="search"></select-three> -->
    <div v-loading="loading">
      <base-table :tableInitData="tableInitData" :tableData="tableData" :width="150"></base-table>
    </div>
  </div>
</template>

<script>
import BaseTable from '@/components/basic/baseTable'
// import SelectThree from '@/components/basic/selectThree'
import axios from 'axios'
export default {
  components: {
    BaseTable,
    // SelectThree
  },
  data() {
    return {
      loading: false,
      tableInitData: [
          ['olt_ip', "OLT IP"],
          ['olt_up_port', "上联口编号"],
          ['threshold', "上联阈值"],
          ['peak_rate', '峰值端口占用率'],
          ['start_time', "开始时间点"],
          ['end_time', "种植时间点"],
          ['particle_size', "流量统计时间粒度"],
          ['user_num', '下挂用户数'],
      ],
      tableData: [
        {
          end_time:"2019/4/7 00:00",
          id:1,
          olt_ip:"10.45.36.16",
          olt_up_port:"0/0/2/0",
          particle_size:"1 day",
          peak_rate:0.34,
          start_time:"2019/4/6 00:00",
          threshold:"50%",
          user_num:378
        }
      ],
    };
  },
  mounted() {
    axios({
        method: "get",
        url: "/api/olt_up_port_lowwer_than_threshold/"
      }).then((res) => {
        if(res.data.success) {
          this.tableData = res.data.data
        }
      })
  },
  methods: {
    search() {

    }
  },
}
</script>

<style>
</style>



