<template>
  <div>
    <h1>流量阈值设置</h1>
    <!-- <el-table :data="portTypeData" style="width: 100%" height="500" v-show="port_visible" :header-cell-style = "header">
      <el-table-column prop="olt_id" label="OLTIP" width="180"></el-table-column>
      <el-table-column prop="slot_number1" label="slot_number1" width="180"></el-table-column>
      <el-table-column prop="trunk_or_north_port_number" label="trunk_or_north_port_number" width="180"></el-table-column>
      <el-table-column prop="port_type1" label="port_type1" width="180"></el-table-column>
      <el-table-column prop="equip_type1" label="equip_type1" width="180"></el-table-column>
      <el-table-column prop="used" label="used" width="180"></el-table-column>
    </el-table>-->
    <base-table
      :tableInitData="tableInitData"
      :tableData="tableData"
      :width="180"
      @getRowData="handleClick"
    ></base-table>
  </div>
</template>

<script>
import BaseTable from "@/components/basic/baseTable3";
import Axios from 'axios';
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        ['device_type','设备类型'],
        ['port_type','端口指向'],
        ['port_level','端口类型'],
        ['down_direction_warn_up_limit','下行告警阈值'],
        ['up_direction_warn_up_limit','上行告警阈值'],
        ['down_direction_start_up_limit','下行启动阈值'],
        ['up_direction_start_up_limit','上行启动阈值'],
        ['user_num_up_boundray','用户百分比阈值'],
        ['FTTB_split_user_num_up_boundary','FTTB用户阈值'],
      ],
      tableData: []
    };
  },
  mounted() {
    // show_port_threshold;
    Axios('/api/show_port_threshold/').then(res => {
      if(res.data.success) {
        this.tableData = res.data.data
      }
    })
  },
  methods: {
    handleClick(e) {
      console.log(e);
    }
  }
};
</script>

<style>
</style>



