<template>
  <div>
    <h1>端口流量异常及超限告警</h1>
    <select-three :search="search"></select-three>
    <div class="block">
    <span class="demonstration">默认</span>
    <el-date-picker
      v-model="date"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
  </div>
    <el-select v-model="select1">
      <el-option v-for="item in options1" :key="item" :label="item" :value="item"></el-option>
    </el-select>
    <el-select v-model="select2">
      <el-option v-for="item in options2" :key="item" :label="item" :value="item"></el-option>
    </el-select>
    <div v-loading="loading">
      <base-table :tableInitData="tableInitData" :tableData="tableData" :width="180"></base-table>
    </div>
  </div>
</template>

<script>
import BaseTable from "@/components/basic/baseTable";
import SelectThree from "@/components/basic/selectThree";
import axios from "axios";
export default {
  components: {
    BaseTable,
    SelectThree
  },
  data() {
    return {
      loading: false,
      tableInitData: [
        ["olt_ip", "OLT IP"],
        ["olt_port", "PON编号"],
        ["threshold", "上联阈值"],
        ["peak_rate", "峰值端口占用率"],
        ["start_time", "开始时间点"],
        ["end_time", "种植时间点"],
        ["particle_size", "流量统计时间粒度"],
        ["user_num", "下挂用户数"]
      ],
      tableData: [
        {
          city_name: "天津",
          department_name: "东丽分公司",
          end_time: "2019/4/7 00:00",
          olt: "DLLZZ_ZX_OLT04",
          olt_ip: "10.45.36.16",
          olt_port: "0/0/5/3",
          particle_size: "1 day",
          peak_rate: 0.0242,
          start_time: "2019/4/6 00:00",
          station: "DLLZZ",
          threshold: "50%",
          user_num: 3
        }
      ],
      options1: [
        '上联口上行超预警阈值',
        '上联口上行超启动扩容阈值',
        '上联口下行超预警阈值',
        '上联口下行超启动扩容阈值',
        'PON口上行超预警阈值',
        'PON口上行超启动扩容阈值',
        'PON口下行超预警阈值',
        'PON口下行超启动扩容阈值'
      ],
      options2: ['单日超限','连续三天超限','连续七天超限'],
      select1: "",
      select2: "",
      date: []
    };
  },
  mounted() {},
  methods: {
    search() {
      this.loading = true;
      let dep = this.$store.getters.getDepartment;
      let station = this.$store.getters.getStation;
      let olt = this.$store.getters.getOlt;
      axios({
        method: "get",
        url: "/api/pon_port_over_threshold/",
        params: {
          department_name: dep,
          city_name: "天津",
          station: station,
          olt_name: olt
        }
      }).then(res => {
        if (res.data.success) {
          this.loading = false;
          this.tableData = res.data.data;
        }
      });
    }
  }
};
</script>

<style>
</style>



