<template>
  <div>
    <h1>设备能力瓶颈预测</h1>
    <div class="tableWrapper12">
      <div>
        <h2>PON口信息表</h2>
        <base-table :tableInitData="tableInitData" :tableData="tableData" :width="130"></base-table>
      </div>
      <div>
        <h2>上联口信息表</h2>
        <base-table :tableInitData="tableInitData1" :tableData="tableData1" :width="130"></base-table>
      </div>
    </div>
  </div>
</template>

<script>
import BaseTable from "@/components/basic/baseTable";
import axios from "axios";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        ['id','id'],
        ["OLT_IP","OLT_IP"],
        ['OLT_port','端口'],
        ['capacity','端口能力'],
        ['threshold','端口阈值'],
        ['real_traffic','实际流量'],
      ],
      tableData: [],
      tableInitData1: [
        ['id','id'],
        ['link_ip','上联口组'],
        ['capacity','端口能力'],
        ['threshold','端口阈值'],
        ['real_traffic','实际流量']
      ],
      tableData1: [],
    };
  },
  mounted() {
    axios.get("/api/device_ability_11_1/").then(res => {
      if (res.data.success) {
        this.tableData = res.data.data['PON'];
        this.tableData1 = res.data.data['OLT_UP'];
      }
    });
  }
};
</script>

<style>
.tableWrapper12 {
  display: flex;
  justify-content: space-between
}
</style>



