import networkOpAnalysis from '../networkOpAnalysis'

import Ts10_3 from '../components/ten/ts10_3'
import Ts10_4 from '../components/ten/ts10_4'

import Ts11_1 from '../components/eleven/ts11_1'
import Ts11_2 from '../components/eleven/ts11_2'
import Ts11_3 from '../components/eleven/ts11_3'

import Ts12 from '../components/twelve/ts12'

const routes = [
  {
    path: '/networkOpAnalysis',
    name: '网络优化分析',
    components: {
      businessModel: networkOpAnalysis
    },
    children: [
      // ten
      {
        path: 'ten/ts10_3',
        name: '任务10-3',
        components: {
          appRight: Ts10_3
        }
      }, {
        path: 'ten/ts10_4',
        name: '任务10-4',
        components: {
          appRight: Ts10_4
        }
      },
      // eleven
      {
        path: 'eleven/ts11_1',
        name: '任务11-1',
        components: {
          appRight: Ts11_1
        }
      }, {
        path: 'eleven/ts11_2',
        name: '任务11-2',
        components: {
          appRight: Ts11_2
        }
      }, {
        path: 'eleven/ts11_3',
        name: '任务11-3',
        components: {
          appRight: Ts11_3
        }
      },
      // twelve
      {
        path: 'twelve/ts12',
        name: '任务12',
        components: {
          appRight: Ts12
        }
      },
    ]
  }
]
export default routes



