<template>
  <div>
    <h1>用户累计流量信息表</h1>
    <base-table :tableInitData="tableInitData" :tableData="tableData" :width="190"></base-table>
  </div>
</template>

<script>
import axios from "axios";
import BaseTable from "@/components/basic/baseTable";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        ["id",'ID'],
        ["OLT_IP",'OLT_IP'],
        ["OLT_PORT",'OLT_PORT'],
        ["is_LAN",'是否LAN用户'],
        ["MDU_ip",'MDU_ip'],
        ["MDU_port",'MDU_port'],
        ["SPEED",'套餐'],
        ["LOGIN_NAME",'宽带账号'],
        ["if_multiple",'是否组播用户'],
        ["up_multiple_user_live",'上行组播用户直播带宽（Mbps）'],
        ["up_multiple_user_demand",'上行组播用户点播带宽（Mbps）'],
        ["up_unicast_live",'上行单播用户直播（Mbps）'],
        ["up_unicast_demand",'上行单播用户点播（Mbps）'],
        ["up_bandwidth",'上行上网带宽（Mbps）'],
        ["up_sum",'上行带宽总和（Mbps）'],
        ["down_multiple_user_live",'下行组播用户直播带宽（Mbps）'],
        ["down_multiple_user_demand",'下行组播用户点播带宽（Mbps）'],
        ['down_unicast_user_live','下行单播用户直播（Mbps）'],
        ['down_unicast_demand','下行单播用户点播（Mbps）'],
        ['down_bandwidth','下行上网带宽（Mbps）'],
        ['down_sum','下行带宽总和（Mbps）'],
      ],
      tableData: []
    };
  },
  mounted() {
    axios.get("/api/user_table_9_1/").then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  }
};
</script>

<style>
</style>



