<template>
    <el-table :data="tableData" style="width: 100%" height="700" :header-cell-style = "header">
      <el-table-column
        v-for="(columnData,index) in tableInitData"
        :key="index"
        :prop="columnData"
        :label="columnData"
        :width="width"
      ></el-table-column>
    </el-table>
</template>

<script>
import axios from 'axios'
// tableInitData [[prop,label]]
export default {
  props: {
    tableInitData: {
      type: Array,
      default: []
    },
    tableData: {
      type: Array,
      default: []
    },
    width: {
      type: Number,
      default: 180
    }
  },
  methods: {
    header(row, column, rowIndex, columnIndex) {
      return {
        background:'#85C1E9',
        color: '#17202A'
      }
    }
  }
}
</script>

<style>

</style>



