<template>
  <div>
    <div>9-2</div>
    <base-table :tableInitData="tableInitData" :tableData="tableData" :width="180"></base-table>
  </div>
</template>

<script>
import axios from "axios";
import BaseTable from "@/components/basic/baseTable1";
export default {
  components: {
    BaseTable
  },
  data() {
    return {
      tableInitData: [
        "id",
        "OLT_IP",
        "OLT_PORT",
        "is_LAN",
        "MDU_ip",
        "MDU_port",
        "SPEED",
        "LOGIN_NAME",
        "if_multiple",
        "up_multiple_user_live",
        "up_multiple_user_demand",
        "up_unicast_live",
        "up_unicast_demand",
        "up_bandwidth",
        "up_sum",
        "down_multiple_user_live",
        "down_multiple_user_demand",
        'down_unicast_user_live',
        'down_unicast_demand',
        'down_bandwidth',
        'down_sum'
      ],
      tableData: []
    };
  },
  mounted() {
    axios.get("/api/user_table_9_1/").then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  }
};
</script>

<style>
</style>



