<template>
  <div class="app-container">
    <div class="app-side app-side-left">
      <sidebar :data="sidebardata" :open="defultopen"></sidebar>
    </div>
    <div class="app-right" style="overflow-y: auto; position:relative">
      <router-view name="appRight"></router-view>
    </div>
  </div>
</template>

<script>
import sidebar from "@/components/sidebar";
export default {
  data() {
    return {
      defultopen: [
        "/networkBottleneckAnalysis",
        "/networkBottleneckAnalysis/networkDevice"
      ],
      sidebardata: {
        name: "网络瓶颈分析",
        path: "/networkBottleneckAnalysis",
        children: [
          {
            name: "网络能力统计",
            path: "/networkBottleneckAnalysis/networkDevice",
            children: [
              {
                name: "网络能力总览图",
                path: "/networkBottleneckAnalysis/networkDevice/ts51"
              },
              {
                name: "网络设备连接拓扑图",
                path: "/networkBottleneckAnalysis/networkDevice/ts52"
              },
              {
                name: "设备端口/能力对接适配性检验",
                path: "/networkBottleneckAnalysis/networkDevice/ts54"
              },
              {
                name: "OLT上联口北向设备间连接关系",
                path: "/networkBottleneckAnalysis/networkDevice/ts55"
              },
              {
                name: "OLT PON口南向设备间连接关系 ",
                path: "/networkBottleneckAnalysis/networkDevice/ts56"
              },
              {
                name: "网络能力链路树",
                path: "/networkBottleneckAnalysis/networkDevice/ts58"
              }
            ]
          },
          {
            name: "压力分析",
            path: "/networkBottleneckAnalysis/stressAnalysis",
            children: [
              {
                name: "套餐流量特征",
                path: "/networkBottleneckAnalysis/stressAnalysis/ts91"
              },
              {
                name: "用户基本流量信息表",
                path: "/networkBottleneckAnalysis/stressAnalysis/ts92"
              },
              {
                name: "MDU累计流量信息表",
                path: "/networkBottleneckAnalysis/stressAnalysis/ts93"
              },
              {
                name: "PON口累计流量信息表",
                path: "/networkBottleneckAnalysis/stressAnalysis/ts94"
              },
              {
                name: "上联口组累计流量信息表",
                path: "/networkBottleneckAnalysis/stressAnalysis/ts95"
              },
              {
                name: "设定用户带宽模型",
                path: "/networkBottleneckAnalysis/stressAnalysis/ts911"
              },
              {
                name: '设备流量查看',
                path: "/networkBottleneckAnalysis/stressAnalysis/ts916"
              }
            ]
          },
        ]
      }
    };
  },
  components: {
    sidebar
  }
};
</script>

<style>
.app-container {
  position: relative;
  padding: 0;
  margin: 0;
  height: calc(100% - 95px);
  width: 100%;
  display: flex;
}
.app-side {
  height: 100%;
  width: 250px;
  font-weight: 700;
  color: #333;
  background-color: #fff;
  /* border-right: solid 1px grey; */
  overflow-y: scroll;
}
.app-right {
  width: calc(100% - 230px);
  height: calc(100% - 40px);
  padding: 20px;
  overflow-y: scroll;
  overflow: auto;
}
</style>



