<template>
  <div class="app-container">
    <div class="app-side app-side-left">
      <sidebar :data="sidebardata" :open="defultopen"></sidebar>
    </div>
    <div class="app-right" style="overflow-y: auto; position:relative">
      <router-view name="appRight"></router-view>
    </div>
  </div>
</template>

<script>
import sidebar from "@/components/sidebar";
export default {
  data() {
    return {
      defultopen: ["/userActionAnalysis", "/userActionAnalysis/userInfo"],
      sidebardata: {
        name: "用户行为分析",
        path: "/userActionAnalysis",
        children: [
          {
            name: "用户信息",
            path: "/userActionAnalysis/userInfo",
            children: [
              {
                name: "本地网用户－时间特性统计",
                path: "/userActionAnalysis/userInfo/ts31"
              },
              // {
              //   name: "用户并发率等统计",
              //   path: "/userActionAnalysis/userInfo/ts32"
              // }
            ]
          },
          {
            name: "OLT内的用户分布",
            path: "/userActionAnalysis/userDistribution",
            children: [
              {
                name: "OLT下挂用户分布",
                path: "/userActionAnalysis/userDistribution/oltUsersCount"
              },
              {
                name: "分业务带宽的用户数分布",
                path: "/userActionAnalysis/userDistribution/usersStatA"
              },
              {
                name: "IPTV用户数分布",
                path: "/userActionAnalysis/userDistribution/usersStatIPTV"
              },
              {
                name: "分业务类型的用户数分布",
                path: "/userActionAnalysis/userDistribution/usersStat"
              }
            ]
          }
        ]
      }
    };
  },
  components: {
    sidebar
  }
};
</script>

<style>
.app-container {
  position: relative;
  padding: 0;
  margin: 0;
  height: calc(100% - 95px);
  width: 100%;
  display: flex;
}
.app-side {
  height: 100%;
  width: 250px;
  font-weight: 700;
  color: #333;
  background-color: #fff;
  /* border-right: solid 1px grey; */
  overflow-y: scroll;
}
.app-right {
  width: calc(100% - 230px);
  height: calc(100% - 40px);
  padding: 20px;
  overflow-y: scroll;
  overflow: auto;
}
</style>



