<template>
  <div class="trafficStat">
    <h1>天津全网上联链路端口占用率统计</h1>
    <div id="pon_rate_chart5" style="height:500px;margin-top:20px"></div>
  </div>
</template>

<script>
import Loading from "@/components/loading";
import echarts from "echarts";
import axios from "axios";
export default {
  name: "ts811",
  data() {
    return {
      msg: "",
      isLoading: false,
      trafficStatPie: [],
      trafficStatBar: []
    };
  },
  components: {
    Loading
    // selectThree
  },
  // computed:{
  //     city(){
  //        return  this.$store.getters.getCity
  //     },
  //     department(){
  //        return  this.$store.getters.getDepartment
  //     },
  //     station(){
  //        return  this.$store.getters.getStation
  //     },
  //     olt(){
  //         return this.$store.getters.getOLT
  //     }
  // },
  methods: {
    initChart(chart, datapie, databar) {
      let myChart = echarts.init(document.getElementById(chart));
      myChart.clear();
      let option = {
        color: [
          "#31C5C0",
          "#A0CE3A",
          "#1E9BD1",
          "#0F347B",
          "#585247",
          "#7F6AAD",
          "#009D85",
          "#CD8162",
          "#CDC9A5",
          "#CD6600",
          "#DAA520",
          "#6B8E23",
          "#9F79EE"
        ],
        backgroundColor: "#fff",
        tooltip: {
          trigger: "axis"
        },
        toolbox: {
          feature: {
            saveAsImage: { show: true, backgroundColor: "#fff" }
          }
        },
        grid: { top: "10%", left: "5%", right: "50%" },
        legend: {
          orient: "vertical",
          x: "right",
          y: "center",
          data: datapie
        },
        calculable: true,
        xAxis: [
          {
            type: "category",
            data: databar[0]
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "端口数"
          }
        ],
        series: [
          {
            name: "端口数",
            type: "bar",
            barMaxWidth: "100",
            data: databar[1]
          },
          {
            name: "数量",
            type: "pie",
            radius: ["20%", "35%"],
            center: ["75%", "50%"],
            label: {
              normal: {
                formatter: " {b|{b}}\n {hr|}\n {c} {per|{d}%}  ",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                rich: {
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 14,
                    lineHeight: 20,
                    align: "center"
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              }
            },
            data: datapie
          }
        ]
      };
      myChart.setOption(option);
      window.onresize = function() {
        myChart.resize();
      };
    },
    _createTrafficStat(data) {
      this.trafficStatPie = [];
      this.trafficStatBar = [];
      this.trafficStatPie = [
        {
          name: "0-20%",
          value: data["0-20"]
        },
        {
          name: "20-40%",
          value: data["20-40"]
        },
        {
          name: "40-60%",
          value: data["40-60"]
        },
        {
          name: "60-80%",
          value: data["60-80"]
        },
        {
          name: "80-100%",
          value: data["80-100"]
        }
      ];
      let tempx = ["0-20%", "20-40%", "40-60%", "60-80%", "80-100%"];
      let tempy = [
        data["0-20"],
        data["20-40"],
        data["40-60"],
        data["60-80"],
        data["80-100"]
      ];
      this.trafficStatBar.push(tempx);
      this.trafficStatBar.push(tempy);
    }
  },
  mounted() {
    this.isLoading = true;
    axios
      .get("/api/city_up_road_traffic/")
      .then(response => {
        this._createTrafficStat(response.data.data);
        this.initChart(
          "pon_rate_chart5",
          this.trafficStatPie,
          this.trafficStatBar
        );
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



