<template>
  <div>
    <h1>套餐流量-时间趋势</h1>
    <span>纵坐标单位：Mbps</span>
    <base-line-chart
      :width="800"
      :height="600"
      :title="'时间-峰值'"
      :legend="legend"
      :xData="xData"
      :yData="yData"
      :id="'chart'"
    ></base-line-chart>
    <span>纵坐标单位：Mbps</span>
    <base-line-chart
      :width="800"
      :height="600"
      :title="'时间-均值'"
      :legend="legend"
      :xData="xData"
      :yData="yData_1"
      :id="'chart1'"
    ></base-line-chart>
    <!-- <base-line-chart
      :width="800"
      :height="600"
      :title="'时间-用户数'"
      :legend="legend"
      :xData="xData"
      :yData="yData_2"
      :id="'chart2'"
    ></base-line-chart>
    <base-line-chart
      :width="800"
      :height="600"
      :title="'时间-pon口数'"
      :legend="legend"
      :xData="xData"
      :yData="yData_3"
      :id="'chart3'"
    ></base-line-chart> -->
  </div>
</template>

<script>
import BaseLineChart from "@/components/basic/baseChart/baseLineChart";
import axios from "axios";
export default {
  components: {
    BaseLineChart
  },
  data() {
    return {
      title: "BARS-时间流量",
      legend: [],
      xData: [],
      yData: [],
      yData_1: [],
      yData_2: [],
      yData_3: [],
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/user_featrue_trend_line_8_5/"
    }).then(res => {
      if (res.data.success) {
        // console.log(res.data.data)
        // let re = res.data.data
        // let x = []
        // let y_300_pon_port_count = []
        // let y_in = []
        // // re.map((item,index) => {
        // //   x.push(`day${index + 1}`)
        // //   y_out.push(item.traffic)
        // // })
        // this.legend = ['out','in']
        // // this.xData = x
        // // this.yData = [{
        // //   name: 'CDN',
        // //   type: "line",
        // //   data: y
        // // }]
        // console.log(re[0].day_out)
        // let x = ['day1','day2','day3','day4','day5','day6','day7']
        // this.xData = x
        // this.yData = [{
        //   name: 'out',
        //   type: "line",
        //   data: re[0].day_out
        // },{
        //   name: 'in',
        //   type: "line",
        //   data: re[0].day_in
        // }]
        let re = res.data.data;
        let data_200 = this.initData(re["200M"]);
        let data_300 = this.initData(re["300M"]);
        let data_500 = this.initData(re["500M"]);
        this.legend = ["200M", "300M", "500M"];
        this.xData = data_200.date;
        this.yData = [
          {
            name: "200M",
            type: "line",
            data: data_200.downstream_peak
          },
          {
            name: "300M",
            type: "line",
            data: data_300.downstream_peak
          },
          {
            name: "500M",
            type: "line",
            data: data_500.downstream_peak
          }
        ];
        this.yData_1 = [
          {
            name: "200M",
            type: "line",
            data: data_200.downstream_avg
          },
          {
            name: "300M",
            type: "line",
            data: data_300.downstream_avg
          },
          {
            name: "500M",
            type: "line",
            data: data_500.downstream_avg
          }
        ];
        this.yData_2 = [
          {
            name: "200M",
            type: "line",
            data: data_200.user_count
          },
          {
            name: "300M",
            type: "line",
            data: data_300.user_count
          },
          {
            name: "500M",
            type: "line",
            data: data_500.user_count
          }
        ];
        this.yData_3 = [
          {
            name: "200M",
            type: "line",
            data: data_200.pon_port_count
          },
          {
            name: "300M",
            type: "line",
            data: data_300.pon_port_count
          },
          {
            name: "500M",
            type: "line",
            data: data_500.pon_port_count
          }
        ];
      }
    });
  },
  methods: {
    initData(data) {
      let date = [];
      let d1 = [];
      let d2 = [];
      let d3 = [];
      let d4 = [];
      data.map(item => {
        date.push(item.date);
        d1.push(item.pon_port_count);
        d2.push(item.downstream_avg);
        d3.push(item.user_count);
        d4.push(item.downstream_peak);
      });
      let obj = {
        date: date,
        pon_port_count: d1,
        downstream_avg: d2,
        user_count: d3,
        downstream_peak: d4
      };
      return obj;
    }
  }
};
</script>

<style>
</style>



