import system from '../system'

import manage from '../components/manage'
import Ts8_17 from '../components/ts8_17'

import Ts21 from '@/components/task/two/ts21'
import Ts22 from '@/components/task/two/ts22'
import Ts23 from '@/components/task/two/ts23'
import Ts24 from '@/components/task/two/ts24'

const routes = [
  {
    path: '/system',
    name: '网络优化分析',
    components: {
      businessModel: system
    },
    children: [
      {
        path: 'manage/id',
        components: {
          appRight: manage
        }
      },
      // 参数设置
      {
        path: 'parameterSet/ts21',
        name: '任务2-1',
        components: {
          appRight: Ts21
        }
      },
      {
        path: 'parameterSet/ts22',
        name: '任务2-2',
        components: {
          appRight: Ts22
        }
      },
      {
        path: 'parameterSet/ts23',
        name: '任务2-3',
        components: {
          appRight: Ts23
        }
      },
      {
        path: 'parameterSet/ts24',
        name: '任务2-4',
        components: {
          appRight: Ts24
        }
      },
      {
        path: 'parameterSet/ts8_17',
        name: '任务8-17',
        components: {
          appRight: Ts8_17
        }
      }
    ]
  }
]
export default routes



