<template>
  <div>
    <h1>设定用户带宽模型</h1>
    <el-table :data="user_bandwidth_model_table_data" style="width: 100%" height="550">
      <el-table-column prop="model_type" label="model_type" width="180" fixed></el-table-column>
      <el-table-column
        prop="vedio_user_infiltrate_rate"
        label="vedio_user_infiltrate_rate"
        width="180"
      ></el-table-column>
      <el-table-column prop="vedio_concurrence_rate" label="vedio_concurrence_rate"></el-table-column>
      <el-table-column
        prop="boardband_concurrence_rate"
        label="boardband_concurrence_rate"
        width="180"
      ></el-table-column>
      <el-table-column prop="boardband_user_avg_down" label="boardband_user_avg_down"></el-table-column>
      <el-table-column prop="live_4K" label="live_4K" width="180"></el-table-column>
      <el-table-column prop="live_HD" label="live_HD"></el-table-column>
      <el-table-column prop="live_SD" label="live_SD" width="180"></el-table-column>
      <el-table-column prop="demand_user_avg_down" label="demand_user_avg_down"></el-table-column>
      <el-table-column
        prop="peak_time_live_user_ratio"
        label="peak_time_live_user_ratio"
        width="180"
      ></el-table-column>
      <el-table-column prop="peak_time_demand_user_ratio" label="peak_time_demand_user_ratio"></el-table-column>
      <el-table-column prop="vedio_avg_down" label="vedio_avg_down" width="180"></el-table-column>
      <el-table-column prop="boardband_avg_down" label="boardband_avg_down"></el-table-column>
      <el-table-column prop="sum_down" label="用户速率" width="180"></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="修改参数" :visible.sync="dialogFormVisible">
      <div>
        <el-input v-model="model_type" placeholder="model_type" width="180" fixed></el-input>
        <el-input v-model="vedio_user_infiltrate_rate" placeholder="vedio_user_infiltrate_rate" width="180"></el-input>
        <el-input v-model="vedio_concurrence_rate" placeholder="vedio_concurrence_rate"></el-input>
        <el-input v-model="boardband_concurrence_rate" placeholder="boardband_concurrence_rate" width="180"></el-input>
        <el-input v-model="boardband_user_avg_down" placeholder="boardband_user_avg_down"></el-input>
        <el-input v-model="live_4K" placeholder="live_4K" width="180"></el-input>
        <el-input v-model="live_HD" placeholder="live_HD"></el-input>
        <el-input v-model="live_SD" placeholder="live_SD" width="180"></el-input>
        <el-input v-model="demand_user_avg_down" placeholder="demand_user_avg_down"></el-input>
        <el-input v-model="peak_time_live_user_ratio" placeholder="peak_time_live_user_ratio" width="180"></el-input>
        <el-input v-model="peak_time_demand_user_ratio" placeholder="peak_time_demand_user_ratio"></el-input>
        <el-input v-model="vedio_avg_down" placeholder="vedio_avg_down" width="180"></el-input>
        <el-input v-model="boardband_avg_down" placeholder="boardband_avg_down"></el-input>
        <el-input v-model="sum_down" placeholder="用户速率" width="180"></el-input>
        <!-- <el-input v-model="cu_1_ratio" placeholder="请输入所占比例" class="elInput"></el-input> -->
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateNew">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      dialogFormVisible: false,
      user_bandwidth_model_table_data: [],
      model_type: '',
      vedio_user_infiltrate_rate: '',
      vedio_concurrence_rate: '',
      boardband_concurrence_rate: '',
      boardband_user_avg_down: '',
      live_4K: '',
      live_HD: '',
      live_SD: '',
      demand_user_avg_down: '',
      peak_time_live_user_ratio: '',
      peak_time_demand_user_ratio: '',
      vedio_avg_down: '',
      boardband_avg_down: '',
      sum_down: '',
      id: ''
    }
  },
  mounted() {
    axios.get('/api/user_bandwidth_model_table_show_9_11/').then((res) => {
      if(res.data.success) {
        this.user_bandwidth_model_table_data = res.data.data
      }
    })
  },
  methods: {
    handleClick(e) {
      this.dialogFormVisible = true;
      this.id = e.id;
      this.vedio_concurrence_rate = e.vedio_concurrence_rate;
      this.demand_user_avg_down = e.demand_user_avg_down
      this.live_4K = e.live_4K;
      this.boardband_concurrence_rate = e.boardband_concurrence_rate;
      this.live_SD = e.live_SD
      this.sum_down = e.sum_down
      this.peak_time_demand_user_ratio = e.peak_time_demand_user_ratio
      this.peak_time_live_user_ratio = e.peak_time_live_user_ratio
      this.boardband_avg_down = e.boardband_avg_down
      this.model_type = e.model_type
      this.vedio_user_infiltrate_rate = e.vedio_user_infiltrate_rate
      this.boardband_user_avg_down = e.boardband_user_avg_down
      this.id = e.id
      this.live_HD = e.live_HD
      this.vedio_avg_down = e.vedio_avg_down
    },
    updateNew() {
      axios({
        method: "get",
        url: "/api/user_bandwidth_model_table_update_9_11/",
        params: {
          id: this.id,
          vedio_concurrence_rate: this.vedio_concurrence_rate,
          demand_user_avg_down: this.demand_user_avg_down,
          live_4K: this.live_4K,
          boardband_concurrence_rate: this.boardband_concurrence_rate,
          live_SD: this.live_SD,
          sum_down: this.sum_down,
          peak_time_demand_user_ratio: this.peak_time_demand_user_ratio,
          peak_time_live_user_ratio: this.peak_time_live_user_ratio,
          boardband_avg_down: this.boardband_avg_down,
          model_type: this.model_type,
          vedio_user_infiltrate_rate: this.vedio_user_infiltrate_rate,
          boardband_user_avg_down: this.boardband_user_avg_down,
          live_HD: this.live_HD,
          vedio_avg_down: this.vedio_avg_down,
        }
    })
  },
  }
};
</script>

<style>
</style>



