<template>
  <div class="app-container">
    <div class="app-side app-side-left">
      <sidebar :data="sidebardata" :open="defultopen"></sidebar>
    </div>
    <div class="app-right" style="overflow-y: auto; position:relative">
      <router-view name="appRight"></router-view>
    </div>
  </div>
</template>

<script>
import sidebar from "@/components/sidebar";
export default {
  data() {
    return {
      defultopen: ["/networkResourceAnalysis", "/networkResourceAnalysis/olt"],
      sidebardata: {
        name: "网络资源分析",
        path: "/networkResourceAnalysis/olt",
        children: [
          {
            name: "OLT分析",
            path: "/networkResourceAnalysis/olt/olt",
            children: [
              {
                name: "OLT数量及各厂家占比",
                path: "/networkResourceAnalysis/olt/vendorCount"
              },
              {
                name: "各型号占比",
                path: "/networkResourceAnalysis/olt/modelCount"
              }
            ]
          },
          {
            name: "PON分析",
            path: "/networkResourceAnalysis/pon/pon",
            children: [
              {
                name: "PON口数量及利用率",
                path: "/networkResourceAnalysis/pon/ponCount"
              },
              {
                name: "业务板槽位利用率分布",
                path: "/networkResourceAnalysis/pon/serviceBoardStat"
              },
              {
                name: "PON口制式比例",
                path: "/networkResourceAnalysis/pon/ponType"
              },
              {
                name: "PON口制式厂家比例",
                path: "/networkResourceAnalysis/pon/vendorPonType"
              },
              {
                name: "PON口流量利用率",
                path: "/networkResourceAnalysis/pon/trafficStat"
              },
              {
                name: "PON设备能力分析",
                path: "/networkResourceAnalysis/pon/ts61"
              }
            ]
          },
          {
            name: "上联分析",
            path: "/networkResourceAnalysis/GE/upper",
            children: [
              {
                name: "端口速率分布",
                path: "/networkResourceAnalysis/GE/portCount"
              },
              {
                name: "上联口流量分布",
                path: "/networkResourceAnalysis/GE/trafficRate"
              },
              // {
              //   name: "OLT上联口实时统计",
              //   path: "/networkResourceAnalysis/GE/ts8_1_1"
              // }
            ]
          },
          {
            name: "设备分析",
            path: "/networkResourceAnalysis/equip/onu",
            children: [
              {
                name: "三种ONU的比例",
                path: "/networkResourceAnalysis/equip/ONUType"
              },
              {
                name: "ONU不匹配的用户数",
                path: "/networkResourceAnalysis/equip/ONUnotMatch"
              }
            ]
          },
          {
            name: "宽带网络架构",
            path: "/networkResourceAnalysis/img",
            children: [
              {
                name: "宽带网络架构图",
                path: "/networkResourceAnalysis/img/ts41"
              }
            ]
          }
        ]
      }
    };
  },
  components: {
    sidebar
  }
};
</script>

<style>
.app-container {
  position: relative;
  padding: 0;
  margin: 0;
  height: calc(100% - 95px);
  width: 100%;
  display: flex;
}
.app-side {
  height: 100%;
  width: 250px;
  font-weight: 700;
  color: #333;
  background-color: #fff;
  /* border-right: solid 1px grey; */
  overflow-y: scroll;
}
.app-right {
  width: calc(100% - 230px);
  height: calc(100% - 40px);
  padding: 20px;
  overflow-y: scroll;
  overflow: auto;
}
</style>



